import React, { Component } from 'react';
export default  class Resume extends Component {
  render() {
    let resumeData = this.props.resumeData;
      const divStyle = {
      margin: '40px',
      color: '#fff'
      };
      const pStyle = {
      fontSize: '25px',
      textAlign: 'center'
      };
    return (
      <section id="resume">

         <div className="row education">

            <div className="three columns header-col">
               <h1><span>Education</span></h1>
            </div>

            <div className="nine columns main-col">
              {
                resumeData.education && resumeData.education.map((item)=>{
                  return(
                    <div className="row item">
                       <div className="twelve columns">
                          <h3>{item.UniversityName}</h3>
                          <p className="info">
                          {item.specialization}
                          <span>&bull;</span> <em className="date">{item.MonthOfPassing} {item.YearOfPassing}</em></p>
                          {/*<p>
                          {item.Achievements}
                          </p>*/}
                       </div>
                    </div>
                  )
                })
              }
            </div>
         </div>
        <div className="row work">
            <div className="three columns header-col">
               <h1><span>Work</span></h1>
            </div>

            <div className="nine columns main-col">
              {
                resumeData.work && resumeData.work.map((item) => {
                  return(
                    <div className="row item">
                       <div className="twelve columns">
                          <h3>{item.CompanyName}</h3>
                          <p className="info">
                          {item.specialization}
                          <span>&bull;</span> <em className="date">{item.MonthOfLeaving} {item.YearOfLeaving}</em></p>
                          <p>
                          {item.Achievements}
                          </p>
                       </div>

                    </div>

                  )
                })
              }
            </div>
         </div>


         <div className="row work">

            <div className="three columns header-col">
               <h1><span>Skills</span></h1>
            </div>

            <div className="nine columns main-col">

              {/*
               <p>
               {resumeData.skillsDescription}
               </p>
              */}
              {/*
              <p className="info">
               {resumeData.skillsDescription}
               </p>
              */}
               {
               resumeData.skills && resumeData.skills.map((item) => {
                  return(
                    <div className="row item">
                       <div className="twelve columns">
                         
                       <p className="info">
                          {item.skillname}
                          <br></br>
                          </p>
                         
                         {/*
                          <p className="info">
                          {item.skillname}
                          <span>&bull;</span> <em className="date">{item.skillDescription} {item.YearOfLeaving}</em></p>
                         */}
                       </div>

                    </div>

                  )
                })
              }
              
          {/*
   				<div className="bars">

   				   <ul className="skills">
                {
                  resumeData.skills && resumeData.skills.map((item) => {
                    return(
                      
                      <li>
                        
                      <span className={`bar-expand ${item.skillname.toLowerCase()}`}>
                      </span><em>{item.skillname}</em>
                    
                      <em>{item.skillname}</em>
                      </li>
                      
                    )
                  })
                }

   					</ul>

              </div> */}
   			</div>
         </div>
          
         {/*
         <div style={divStyle}>
           <p style={pStyle}>
          Would you prefer a good old fashioned resume ? Click <a href="./Umesh_M_Resume.pdf" download>here</a> to get it.
          </p>
          </div>
          */}

          <div className="row work">
          <div className="row nine columns main-col">
          <div className="row item">
          <div className="twelve columns">
          <p className="info">
                                     Would you prefer a good old fashioned resume ?  Click <a href="../Umesh_M_Resume.pdf" download>here</a> to get it.
          </p>
          </div>
          </div>
          </div>
          </div>

      </section>    
    );
  }
}